.experience-timeline {
    position: relative;
    padding: 20px 0;
    margin: 40px 0;
}

.experience-timeline::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 4px;
    height: 100%;
    background-color: var(--primary-color);
}

.animate-experience {
    animation: fadeInUp 0.5s forwards;
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Media Query for Responsive Design */
@media (max-width: 768px) {
    .experience-timeline::before {
        left: 0;
        /* Adjust the timeline to start from the left */
    }
}