.certifications-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 30px;
    /* Space between items */
    margin-top: 20px;
}

.cert-card {
    text-align: center;
    /* Center text under image */
    padding: 10px;
    /* Padding around text */
    transition: transform 0.3s ease;
    /* Smooth transition effect */
}

.cert-card:hover {
    transform: translateY(-5px);
    /* Lift effect on hover */
}

.cert-card img {
    width: 100%;
    /* Full width */
    height: auto;
    /* Maintains aspect ratio */
    max-height: 300px;
    /* Adjust max height to fit your images */
    object-fit: contain;
    /* Better fit for PNGs without backgrounds */
    margin-bottom: 10px;
    /* Space between image and text */
}

.cert-card p {
    font-size: 18px;
    /* Font size for certification name */
    font-weight: bold;
    /* Bold text for emphasis */
    color: var(--primary-color);
    /* Color defined in your variables */
    margin-top: 5px;
    /* Space above the text */
    line-height: 1.2;
    /* Improved line height for readability */
}