/* Base styles for theme */
:root {
    --primary: #54a8fc;
    --light: #d9d9d9;
    --dark: #121212;
    --gray: #414344;
    --second: rgba(12, 12, 12, 0.6);
}

/* Container for the toggle */
.toggle-cont {
    position: fixed;
    top: 1%;
    right: 1%;
    width: 50px;
    height: 24px;
    display: flex;
    align-items: center;
    z-index: 1000;
    /* Bring to the front */
}

/* Toggle input, hidden for custom styling */
.toggle-input {
    display: none;
}

/* Toggle label as the visual toggle switch */
.toggle-label {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 24px;
    background-color: var(--gray);
    border-radius: 12px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.toggle-label .slider {
    position: absolute;
    top: 2px;
    left: 2px;
    width: 20px;
    height: 20px;
    background-color: #fff;
    border-radius: 50%;
    transition: transform 0.3s;
}

/* When the toggle is checked, the background and slider position changes */
.toggle-input:checked+.toggle-label {
    background-color: var(--primary);
}

.toggle-input:checked+.toggle-label .slider {
    transform: translateX(26px);
}

/* Optional: add small sparkles for effect */
.toggle-label::before {
    content: '';
    position: absolute;
    width: 8px;
    height: 8px;
    background-color: #f7d259;
    border-radius: 50%;
    top: 6px;
    left: 8px;
    opacity: 0;
    transition: opacity 0.3s;
}

.toggle-input:checked+.toggle-label::before {
    opacity: 1;
}