/* Header.css */

.header {
    text-align: center;
    margin-bottom: 50px;
    position: relative;
}

.profile-image {
    width: 200px;                      /* Width of the image container */
    height: 240px;                     /* Height of the image container */
    border-radius: 50%;                /* Circular image */
    margin: 20px auto 30px;           /* Adjust margin as needed */
    display: block;                    /* Center image */
    border: 4px solid var(--primary-color); /* Border around the image */
    object-fit: cover;                 /* Ensure the image covers the container */
    transition: transform 0.3s ease;   /* Animation effect on hover */
}

.profile-image:hover {
    transform: scale(1.05);
}

.header h1,
.header h2 {
    color: var(--primary-color);
    margin-bottom: 10px;
}

.contact-info {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
    margin-top: 30px;
}

.contact-item {
    display: flex;
    align-items: center;
    transition: color 0.3s ease;
}

.contact-item:hover {
    color: var(--hover-color);
}

.contact-item i {
    margin-right: 10px;
    color: var(--primary-color);
}

.contact-item a {
    color: inherit;
    text-decoration: none;
}

.contact-item a:hover {
    text-decoration: underline;
}